.container {
	width: 100%;
	height: 100%;
	background: rgb(35, 39, 65);
	// background: linear-gradient(to left, rgb(6, 34, 60), #0a1f42);
}

.title {
	text-align: center;
	color: #fff;
	font-size: 26px;
	margin-bottom: 25px;
}

.content {
	width: 600px;
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	padding-bottom: 0;
	border-radius: 10px;
	box-shadow: 0 0 5px #333;
	overflow: hidden;
	background: rgba(0, 0, 0, 0.3);
}


.login_content {
	width: 600px;
	position: relative;
	height: 100%;
	.login_box {
		width: 600px;
		padding: 80px 120px;
		box-sizing: border-box;
		
	}
	.form_Item{
		position: relative;
		transition: all 0.5s;
		opacity: 0;
		top: 40px;
	}
	.showFormItem{
		opacity: 1;
		top: 0;
	}
}
