/******** 滚动条样式 ********/
* {
  outline: none;

  &::-webkit-scrollbar {
    width: 6px;
		height: 6px;
		background: none;
  }

  &::-webkit-scrollbar-button {
    display: none;
  }

  &::-webkit-scrollbar-track {
    border-radius: 3px;
    background-color: #0c4a85;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 3px;
    background-color: #001529;
  }
}

.setting .anticon-setting:hover{
  transform: rotate(150deg);
  transition: all 0.5s;
}
.top_header:hover .logo{
  border-radius: 50%;
  transition: all 0.5s;
}
.top_header:hover .store_name{
  color:#1890ff;
  transition: all 0.5s;
}
.plat_name:hover{
  color:#1890ff;
  transform: scale(1.2);
  transition: all 0.5s;
}

